$(".input").keyup(function () {
  let res = $(this).val();
  let result = res.toLowerCase();

  if (result.includes("seat")) {
    $(".searchItem").addClass("hidden");
    $(".searchItem-seat").removeClass("hidden");
  } else if (result.includes("red")) {
    $(".searchItem").addClass("hidden");
    $(".searchItem-seat-red").removeClass("hidden");
  } else if (result.includes("blue")) {
    $(".searchItem").addClass("hidden");
    $(".searchItem-seat-blue").removeClass("hidden");
  } else if (result.includes("dark")) {
    $(".searchItem").addClass("hidden");
    $(".searchItem-seat-dark").removeClass("hidden");
  } else if (result.includes("white") || result.includes("table")) {
    $(".searchItem").addClass("hidden");
    $(".searchItem-table-white").removeClass("hidden");
  } else if (result.includes("modern") || result.includes("bed")) {
    $(".searchItem").addClass("hidden");
    $(".searchItem-bed-modern").removeClass("hidden");
  } else {
    $(".searchItem").removeClass("hidden");
  }
});

